.button-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.button {
  margin-right: 20px;
  padding: 6px 22px;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  box-shadow: 0px 3px 1px rgba(0, 0, 0, 0.1), 0px 1px 2px rgba(0, 0, 0, 0.08),
    0px 2px 2px rgba(0, 0, 0, 0.12);
}
